.hero {
  height: 275px;
  background-image: url("https://static.smartgurutips.com//uploads/2018/05/Exercise.jpg");
  background-color: lightsalmon;
  background-size: cover;
  background-position: top;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

.title {
  color: white;
  font-size: 165px;
  font-family: 'Shadows Into Light', cursive; 
  text-align: center;
  padding-top: 60px;

}



